import '../sn-itsm-new-record-message';
import '@servicenow/now-modal';
import '@servicenow/now-dropdown';
import {t} from 'sn-translate';

export default ({properties: {payload, title, desc, modal, selectedOption}}) => {
	const modalBody = (payload, description) => {
		const options = payload.map(({displayValue, description, payload}, i) => ({id: `interceptor-type-${i}`, label:displayValue, description, target_payload:payload}));

		return (
			<div id="dropContainer">
				<now-dropdown
					items={options} 
					bare
					size="md" 
					select="single"
					selected-items={[selectedOption]}
				/>
				{description ? (<sn-itsm-new-record-message message={description}/>) : '' }
			</div>
		);
	};

	var modalData = modalBody(payload, desc);
	if (modal === 'true') {
		return (
			<div className="container">
				<now-modal
					component-name="modal0"
					opened={title !== 'Title'}
					size="sm"
					height="md"
					header-label={t(title)}
					footer-actions={[
						{variant: 'primary',label: t('Create'),clickActionType: 'OPEN_TARGET'}, 
						{variant: 'secondary',label: t('Cancel'),clickActionType: 'NOW_MODAL#OPENED_SET'}
					]}>
					{modalData}
				</now-modal>
			</div>
		);
	}
	return (
		<div className="container">
			{modalData}
		</div>
	);
};


