import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {t} from 'sn-translate';

import {
	FETCH_INTERCEPTOR_SUCCESS,
	FETCH_INTERCEPTOR_ERROR
} from './constants';

const interceptorQuery = `query ($queryConditions: String!) {
	GlideRecord_Query {
		aw_record_type_selector (queryConditions: $queryConditions){
		  	_results {
				target_table {
					value,
				  	displayValue
			  	}
				target_field {
			  		value,
					displayValue
				}
				value {
					value
				}
				display_value {
					value
				}
				short_description {
					displayValue
				}
				payload {
					value
				}
		  	}
		}
	}
}`;
	
export const fetchInterceptorEffect = createGraphQLEffect(interceptorQuery, {
	variableList: ['queryConditions'],
	successActionType: FETCH_INTERCEPTOR_SUCCESS,
	errorActionType: FETCH_INTERCEPTOR_ERROR
});

export const optionSelected = ({action, dispatch, properties: {modal}}) => {
	const targetPayload = action.payload.item.target_payload;

	const newProperties = {
		selectedOption: action.payload.item.id,
		selectedPayload: targetPayload,
		desc: action.payload.item.description
	};
	dispatch('PROPERTIES_SET', newProperties);

	//If component is used in ui action modal then dispatch payload to parent modal
	if (modal !== 'true' && targetPayload)
		dispatch('SN_SCRIPTED_MODAL#DATA_SET', targetPayload);
};

export const openTarget = ({dispatch, properties: {selectedPayload}}) => {
	if (selectedPayload) {
		console.log(`Modal Preview Record Payload: ${JSON.stringify(selectedPayload)}`);
		dispatch('PREVIEW_RECORD', selectedPayload);
	}
	//Hide the modal form
	dispatch('PROPERTIES_SET', {title: 'Title'});
};

export const fetchInterceptorSuccess = ({action, dispatch, properties: {target, params, modal}}) => {
	const results = action.payload.data.GlideRecord_Query.aw_record_type_selector._results;
	if (results) {
		var newProps = {};
		
		//Default the payload and description to the first item that will be shown in the dropdown
		//Also check length of descriptions and if longer than 80 characters then show medium modal
		const options = results.map(result => {
			var payload = {};
			if (result.payload.value === null) {
				payload.table = target;
				payload.sys_id = '-1';
				payload.query = `${result.target_field.value}=${result.value.value}`;

				if (params)
					payload.query += `^${params}`;

			} else {
				payload = JSON.parse(result.payload.value);
				//Convert params to valid JSON
				if (payload.params) {
					//Parse the params as this is passed in as a string
					const tmpParams = JSON.parse(payload.params);
					if (params)
						tmpParams.query = params;
									
					payload.params = tmpParams;
				}
			}

			return {
				description : result.short_description.displayValue,
				value : result.display_value.value,
				displayValue : result.display_value.value,
				tableValue : result.target_table.displayValue,
				payload
			};
		});

		if (options.length > 0) {
			newProps.title = t('New {0}', options[0].tableValue);
			newProps.selectedPayload = options[0].payload;
			newProps.desc = options[0].description;
		}

		newProps.payload = options;
		dispatch('PROPERTIES_SET', newProps);

		//Check if we not in a modal and set a default payload in parent payload
		if (modal !== 'true' && newProps.selectedPayload)
			dispatch('SN_SCRIPTED_MODAL#DATA_SET', newProps.selectedPayload);
	}
};
	
export const fetchInterceptorError = ({action}) => {
	console.log('[fetchInterceptorError] result: ', action.payload.message);
};
