import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {
	fetchInterceptorEffect,
	fetchInterceptorSuccess,
	fetchInterceptorError,
	optionSelected,
	openTarget

} from './effects';
import {
	FETCH_INTERCEPTOR_SUCCESS,
	FETCH_INTERCEPTOR_ERROR
} from './constants';
import view from './view';
import styles from './_styles.scss';

createCustomElement('sn-itsm-new-record-interceptor', {
	renderer: {type: snabbdom},
	view,
	styles,
	properties: {
		payload: {default: []},
		title: {default: 'Title'},
		selectedOption: {default: 'interceptor-type-0'},
		selectedPayload: {default: ''},
		modal: {default: 'true'},
		desc: {default: ''},
		target: {default: ''},
		params: {default: ''}
	},
	actionHandlers: {
		INTERCEPTOR_FETCHED: fetchInterceptorEffect,
		[FETCH_INTERCEPTOR_SUCCESS]: fetchInterceptorSuccess,
		[FETCH_INTERCEPTOR_ERROR]: fetchInterceptorError,
		OPEN_TARGET : openTarget,
		'NOW_MODAL#OPENED_SET'({dispatch}) {
			dispatch('PROPERTIES_SET', {title: 'Title'});
		},
		'NOW_DROPDOWN#ITEM_CLICKED': {
			effect: optionSelected,
			stopPropagation: true,
		}
	},
	onBootstrap(host, dispatch) {
		dispatch('INTERCEPTOR_FETCHED', {queryConditions: `target_table=${host.target}^active=true^ORDERBYorder`});
	},
});
